(function() {
  'use strict';

  angular
    .module('nii-website')
    .controller('ProjectsController', ProjectsController);

  /** @ngInject */
  function ProjectsController($timeout, $stateParams, projectService/*, $templateCache*/) {
    var vm = this;
    var projects = projectService.getProjects();
    for (var i = 0; i < projects.length; i++) {
      var project = projects[i];
      if (project.id.toLowerCase() == $stateParams.projectId.toLowerCase()){
        vm.project = project;
      }
    }

  }
})();

